/*=====================
    1.1.Button CSS start
==========================*/
button {
  cursor: pointer;
}
.btn {
  line-height: 20px;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 700;
  border-radius: 0;
  transition: 0.3s ease-in-out;
  &:hover {
    transition: 0.3s ease-in-out;
  }
  &:focus {
    box-shadow: none;
  }
}
.btn-solid {
  padding: 13px 29px;
  color: $white !important;
  letter-spacing: 0.05em;
  border: 2px solid var(--theme-deafult);
  background-image: -webkit-linear-gradient(
    30deg,
    var(--theme-deafult) 50%,
    transparent 50%
  );
  background-image: linear-gradient(
    30deg,
    var(--theme-deafult) 50%,
    transparent 50%
  );
  background-size: 540px;
  background-repeat: no-repeat;
  background-position: 0;
  transition: background 300ms ease-in-out;
  &:hover {
    background-position: 100%;
    color: $black !important;
    background-color: $white;
  }
  &.black-btn {
    background-image: -webkit-linear-gradient(
      30deg,
      $dark-font 50%,
      transparent 50%
    );
    background-image: linear-gradient(30deg, $dark-font 50%, transparent 50%);
    border: 2px solid $dark-font;
  }
  &:focus {
    color: $white;
  }
  &.btn-gradient {
    background: var(--theme-deafult);
    background: linear-gradient(
      180deg,
      var(--gradient1) 0%,
      var(--gradient2) 99%
    );
    transition: background 300ms ease-in-out;
    background-size: 300% 100%;
    border: none;
    &:hover {
      background: linear-gradient(
        -180deg,
        var(--gradient2) 0%,
        var(--gradient1) 99%
      );
      transition: background 300ms ease-in-out;
      color: $white;
    }
  }
  &.btn-green {
    background-image: linear-gradient(
      to right,
      var(--gradient1),
      var(--gradient2)
    );
    border: none;
    background-color: var(--theme-deafult);
    &:hover {
      background-color: var(--theme-deafult);
      background-image: none;
      color: $white;
    }
  }
  &.btn-sm {
    padding: 9px 16px;
  }
  &.btn-xs {
    padding: 5px 8px;
    text-transform: capitalize;
  }
}
.btn-outline {
  display: inline-block;
  padding: 13px 29px;
  letter-spacing: 0.05em;
  border: 2px solid var(--theme-deafult);
  position: relative;
  color: $black;
  &:before {
    transition: 0.5s all ease;
    position: absolute;
    top: 0;
    left: 50%;
    right: 50%;
    bottom: 0;
    opacity: 0;
    content: "";
    background-color: var(--theme-deafult);
    z-index: -2;
  }
  &:hover {
    color: $white;
    background-color: var(--theme-deafult);
    &:before {
      transition: 0.5s all ease;
      left: 0;
      right: 0;
      opacity: 1;
    }
  }
  &:focus {
    &:before {
      transition: 0.5s all ease;
      left: 0;
      right: 0;
      opacity: 1;
    }
  }
}
button {
  &.btn {
    &.btn-solid,
    &.btn-outline {
      &:active {
        background-image: -webkit-linear-gradient(
          30deg,
          var(--theme-deafult) 50%,
          transparent 50%
        );
        background-image: linear-gradient(
          30deg,
          var(--theme-deafult) 50%,
          transparent 50%
        );
        color: $white;
        background: var(--theme-deafult);
      }
    }
  }
}
.btn-classic {
  &:hover {
    background-color: var(--theme-deafult);
  }
}
.btn-white {
  background-color: $white;
  color: var(--theme-deafult);
  transition: all 0.5s ease;
  padding: 10px 29px;
  &:hover {
    color: $black;
    transition: all 0.5s ease;
  }
}
.btn-theme {
  background-color: var(--theme-deafult);
  color: $white;
}
.btn-custom {
  padding: 6px 30px !important;
  border: none;
}
