  }
  .slick-next {
    right: 1px;
    &:before {
      content: "\f105";
      font-size: 10px;
    }
  }
  .slick-prev {
    left: 1px;
    z-index: 1;
    &:before {
      content: "\f104";
      font-size: 10px;
    }
  }
  .home {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    height: 75vh;
