        background-image: url("#{$assetspath}/home-banner/#{$i}.jpg");
      }
    }
  }
  .slider-details {
    position: absolute;
    background-color: rgba($black, 0.1);
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 26%;
    right: 18%;
    text-align: center;
    padding: 55px;
    width: 510px;
    height: 510px;
    &.blue-slide {
      background-color: #223b7d;
    }
    .btn-white {
      margin-top: 15px;
    }
    h1 {
      color: $white;
      font-weight: 700;
    }
    h3 {
      color: $white;
      position: relative;
      text-transform: uppercase;
      display: inline-block;
      &:after,
      &:before {
        content: "";
        position: absolute;
        width: 12px;
        height: 12px;
        background-color: $white;
        border-radius: 100%;
        top: 8px;
      }
      &:after {
        left: -22px;
        top: 8px;
      }
      &:before {
        top: 8px;
        right: -22px;
      }
    }
    h2 {
      font-weight: 400;
      margin-bottom: 0;
      font-size: 45px;
    }
    h4 {
      font-size: 24px;
    }
    h2,
    h3,
    h4 {
      color: $white;
    }
  }
  .slider-contain {
    width: 100%;
    height: 75vh;
    display: flex;
    align-items: center;
    h1 {
      margin-bottom: 0;
      margin-top: 10px;
