/*=====================
    11.Theme tab CSS start
==========================*/
.theme-tab {
  .react-tabs__tab--selected {
    background: unset !important;
    color: var(--theme-deafult);
    border-radius: 0 !important;
    a {
      color: #dafe69 !important;
    }
    .nav-link {
      color: var(--theme-deafult) !important;
    }
  }
}
.tab-content {
  &.nav-material {
    .react-tabs__tab--selected {
      border-bottom: 2px solid var(--theme-deafult) !important;
    }
  }
}
.react-tabs__tab {
  &:focus {
    border-color: transparent !important;
    box-shadow: none !important;
  }
}
.theme-tab {
  position: relative;
  .tab-content {
    display: block;
    &[style="display: block;"] {
      .product-box,
      .tab-box {
        animation: zoomIn 300ms ease-in-out;
      }
    }
    .product-tab {
      .tab-box {
        background-color: $white;
        width: 100%;
        flex: 0 0 25%;
        max-width: calc(25% - 10px);
        margin: 0 5px 10px;
        &:nth-last-child(-n + 4) {
          margin: 0 5px 0;
        }
        .product-box2 {
          img {
            height: 250px;
            padding: 15px;
            transition: all 0.5s ease;
          }
          .media {
            .media-body {
              .rating {
                margin-top: 0;
                i {
                  padding-right: 0;
                  &:nth-child(-n + 4) {
                    color: $star-yellow;
                  }
                  &:last-child {
                    color: $round-border;
                  }
                }
              }
              a {
                h6 {
                  margin-right: 35px;
                  line-height: 17px;
                  margin-top: 5px;
                  margin-bottom: 0;
                }
              }
              h4 {
                font-size: 20px;
                margin-top: 5px;
                font-weight: 700;
                color: $dark-font;
                margin-bottom: 0;
              }
              .color-variant {
                li {
                  display: inline-block;
                  height: 20px;
                  width: 20px;
                  border-radius: 100%;
                  margin-right: 2px;
                  margin-top: 20px;
                  transition: all 0.1s ease;
                  margin-bottom: 0;
                  cursor: pointer;
                }
              }
