          }
        }
      }
    }
  }
  .tab-title,
  .tab-title2 {
    text-align: center;
    font-size: 18px;
    margin-bottom: 25px;
    margin-top: -6px;
    .current {
      a {
        color: var(--theme-deafult);
      }
    }
    li {
      padding-left: 25px;
      padding-right: 25px;
      padding-top: 0;
      line-height: 1;
      cursor: pointer;
      text-transform: capitalize;
    }
    a {
      color: $grey-dark;
      text-transform: uppercase;
    }
  }
  .tab-title2 {
    font-size: 24px;
    font-weight: 700;
    text-transform: uppercase;
    margin-top: -8px;
    &:after {
      content: "";
