}
/*=====================
    12.Banner CSS start
==========================*/
.full-banner {
  position: relative;
  background-size: cover;
  background-repeat: no-repeat;
  padding-top: 190px;
  padding-bottom: 190px;
  background-position: center;
  @for $i from 1 through 28 {
    &.parallax-banner#{$i} {
      background-image: url("#{$assetspath}/parallax/#{$i}.jpg");
    }
  }
  &.parallax-christmas {
    background-image: url("/assets/images/christmas/parallax/1.jpg");
  }
  &.parallax-layout {
    padding-top: 350px;
    padding-bottom: 350px;
    .banner-contain {
      h4 {
        color: $grey-shade;
      }
    }
  }
  &.banner-layout-3 {
    .banner-contain {
      h4 {
        color: $white;
      }
      h3 {
        color: $white;
        margin-top: 10px;
      }
      .color {
        color: var(--theme-deafult);
        line-height: 1;
      }
    }
  }
  &.parallax {
    background-attachment: fixed;
  }
  &.p-left {
    .banner-contain {
      float: left;
    }
  }
  &.p-right {
    .banner-contain {
      float: right;
    }
  }
  &.p-center {
    .banner-contain {
      left: 0;
      right: 0;
      margin: 0 auto;
    }
  }
  &.feature-banner {
    padding: 50px 0;
    h2 {
      text-align: center;
      color: $white;
      margin-bottom: 180px;
      margin-top: -7px;
    }
    .feature-object,
    .feature-object-right {
      text-align: right;
      margin-bottom: -5px;
      li {
        margin-bottom: 55px;
        margin-right: 50px;
        .media {
          img {
            border-radius: 100%;
            border: 1px solid $white;
            margin-left: 15px;
            transition: all 0.5s ease;
          }
          .media-body {
            text-align: right;
            margin-top: 18px;
            h4,
            p {
              color: $white;
            }
            h4 {
              font-size: 20px;
            }
            p {
              margin-bottom: 0;
            }
          }
        }
        &:hover {
          .media {
            img {
              background-color: $white;
              transition: all 0.5s ease;
              transform: scale(1.05);
            }
          }
        }
        &:nth-child(2) {
          padding-right: 30px;
        }
        &:nth-child(3) {
          padding-right: 60px;
          margin-bottom: 0;
        }
      }
    }
    .feature-object-right {
      text-align: left;
      margin-left: 50px;
      li {
        .media {
          img {
            margin-left: 0;
            margin-right: 15px;
          }
          .media-body {
            text-align: left;
          }
        }
        &:nth-child(2) {
          padding-right: 0;
          padding-left: 30px;
        }
        &:nth-child(3) {
          padding-right: 0;
          padding-left: 60px;
        }
      }
    }
    .center-img {
      position: absolute;
      bottom: -112px;
      left: 50%;
      margin: 0 auto;
      transform: translateX(-50%);
    }
    .banner-decor {
      .left-img {
        left: 50px;
        position: absolute;
        top: 0;
        animation: movebounce 4.9s linear infinite;
      }
      .right-img {
        right: 50px;
        position: absolute;
        top: 0;
        animation: movebounce 4.9s linear infinite;
      }
    }
    @keyframes movebounce {
      0% {
        transform: translateY(0px);
      }
      50% {
        transform: translateY(20px);
      }
      100% {
        transform: translateY(0px);
      }
    }
  }
  .banner-contain {
    .btn-solid,
    .btn-outline {
      margin-top: 20px;
    }
    h2 {
      font-size: 100px;
      font-weight: 700;
      color: var(--theme-deafult);
      text-transform: uppercase;
      margin-top: -15px;
    }
    h3 {
      font-size: 60px;
      color: $font-color;
      text-transform: uppercase;
      font-weight: 700;
    }
    h4 {
      font-size: 24px;
      color: $grey;
      font-weight: 600;
      text-transform: uppercase;
      letter-spacing: 0.5em;
      padding-top: 10px;
      margin-bottom: -5px;
      line-height: 1.3;
    }
    &.christmas-contain {
      .btn-solid,
      .btn-outline {
        margin-top: calc(20px + (65 - 20) * ((100vw - 320px) / (1920 - 320)));
      }
      h2 {
        font-size: calc(24px + (70 - 24) * ((100vw - 320px) / (1920 - 320)));
        font-weight: 700;
        color: $white;
        text-transform: capitalize;
        margin-bottom: 0;
        margin-top: 0;
      }
      h3 {
        font-size: calc(20px + (30 - 20) * ((100vw - 320px) / (1920 - 320)));
        color: $white;
        text-transform: capitalize;
        font-weight: 700;
        margin-bottom: 15px;
        margin-top: -6px;
      }
      h4 {
        font-size: calc(18px + (24 - 18) * ((100vw - 320px) / (1920 - 320)));
        color: $white;
        font-weight: 600;
        text-transform: capitalize;
        letter-spacing: 0.07em;
        padding-top: 10px;
        margin-bottom: -5px;
        line-height: 1.3;
        span {
          color: var(--theme-deafult);
        }
      }
    }
  }
  .santa-img {
    position: absolute;
    bottom: -62px;
    left: 28px;
    img {
      display: inline-block;
      animation-name: dance;
      animation-duration: 2s;
      animation-iteration-count: infinite;
      transition-timing-function: cubic-bezier(0.6, -0.28, 0.735, 0.045);
    }
  }
  .decor {
    position: absolute;
    top: -150px;
    left: 0;
  }
  @keyframes dance {
    0% {
      transform: rotate(10deg);
    }
    16.66%,
    49.98% {
      transform: rotate(-10deg);
    }
    32.32% {
      transform: rotate(-5deg);
    }
    66.64%,
    100% {
      transform: rotate(10deg);
    }
    83.8% {
      transform: rotate(5deg);
    }
  }
}
.parallax-cls {
  section {
    &:nth-child(odd) {
      .full-banner {
        background-position: right;
      }
    }
    &:nth-child(even) {
      .full-banner {
        background-position: left;
      }
    }
    .banner-contain {
      margin-top: -12px;
    }
  }
}
