    }
    .cart-wrap {
      bottom: -26px;
      text-align: right;
      left: 0;
      i {
        display: block;
        padding-bottom: 10px;
        padding-top: 10px;
      }
      &.cart-effect-left {
        left: 0;
        right: unset;
      }
    }
    .product-detail,
    .product-info {
      padding-left: 0;
      text-align: center;
      .rating {
        i {
          &:nth-child(-n + 4) {
            color: var(--theme-deafult);
          }
          &:last-child {
            color: #878787;
          }
        }
      }
      h6 {
        line-height: 1;
        margin-bottom: 3px;
        padding-top: 7px;
        padding-bottom: 5px;
        transition: all 0.5s ease;
        font-size: 16px;
        color: $grey4;
        font-weight: 700;
      }
      h4 {
        font-size: 18px;
        color: var(--theme-deafult);
        font-weight: 700;
        margin-bottom: 0;
        transition: all 0.5s ease;
      }
    }
    &:hover {
      .cart-info {
        bottom: 0;
        background-color: var(--theme-deafult);
        opacity: 1;
        transition: all 0.5s ease;
        button {
          animation: none !important;
        }
        a {
          i {
            animation: none !important;
          }
        }
      }
      .product-info {
        .add-btn {
          opacity: 1;
          transition: all 0.2s ease;
          animation: fadeInUp 500ms ease-in-out;
        }
      }
      .img-wrapper {
        .cart-box {
          opacity: 1;
          transition: all 0.2s ease;
          animation: fadeInUp 400ms ease-in-out;
        }
      }
    }
  }
}
header.header-christmas {
  .top-header {
    .header-dropdown {
      li {
        padding-right: 0;
      }
    }
  }
}
.overflow-cls {
  overflow: visible !important;
}
.margin-add {
  margin-top: 60px;
}
.grey-bg {
  background-color: #f5f5f5;
}
.blog-section {
  .blog-details {
    h4 {
      color: var(--theme-deafult);
      margin-top: 15px;
      line-height: 1;
      margin-bottom: 0;
    }
    p {
      color: $dark-font;
      font-size: 14px;
      text-transform: uppercase;
      font-weight: 700;
      line-height: 1.2;
      letter-spacing: 0.03em;
      margin-top: 6px;
      margin-bottom: 6px;
      transition: all 0.5s ease;
    }
    h6 {
      line-height: 1;
      letter-spacing: 0.1em;
      margin-bottom: 0;
    }
  }
  .blog-wrap {
    .blog-image {
      overflow: hidden;
      img {
        transition: all 0.5s ease;
      }
    }
    &:hover {
      .blog-details {
        p {
          color: var(--theme-deafult);
          transition: all 0.5s ease;
        }
      }
      .blog-image {
        img {
          transform: scale(1.2);
          transition: all 0.5s ease;
        }
      }
    }
  }
  .slick-slider {
    .slick-list {
      margin: 0 -12px;
      .slick-slide {
        > div {
          margin: 0 12px;
          position: relative;
        }
      }
    }
  }
  .review-box {
    background-color: $white;
    z-index: 1;
    text-align: center;
    padding: 30px;
    position: relative;
    &:after {
      position: absolute;
      content: "";
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      border: 3px solid var(--theme-deafult);
      opacity: 0.5;
      z-index: -1;
    }
    .santa-img {
      position: absolute;
      top: 0;
      right: -116px;
    }
    .slick-prev {
      z-index: 1;
      top: unset;
      bottom: 0;
      left: -30px;
      right: 0;
      margin: 0 auto;
      &:before {
        font: normal normal normal 14px/1 FontAwesome;
        content: "\f053";
        color: #717171;
      }
      &:after {
        position: absolute;
        content: "";
        height: 13px;
        width: 2px;
        background-color: $white-dark;
        left: 24px;
      }
    }
    .slick-next {
      top: unset;
      left: 30px;
      right: 0;
      margin: 0 auto;
      bottom: 0;
      &:before {
        font: normal normal normal 14px/1 FontAwesome;
        content: "\f054";
        color: #717171;
      }
    }
    .slick-prev {
      left: -30px;
      right: 0;
      margin: 0 auto;
    }
    .review-content {
      .avtar {
        border: 1px solid $white-dark;
        padding: 5px;
        border-radius: 100%;
        display: inline-block;
        margin-top: 10px;
        img {
          border-radius: 100%;
          height: 60px;
          width: auto;
          margin: 0 auto;
          border: 1px solid $white-dark;
        }
      }
      h5 {
        margin-bottom: 0;
        text-transform: uppercase;
        font-weight: 700;
        color: var(--theme-deafult);
        margin-top: 15px;
      }
      h6 {
        margin-bottom: 0;
        margin-top: 10px;
        text-transform: capitalize;
        color: $grey2;
        font-size: 16px;
      }
      p {
        margin-top: 20px;
        margin-bottom: 80px;
        line-height: 24px;
      }
    }
  }
}
